.btn {
  @apply block text-center py-2 hover:bg-gray-700 text-2xl;
}


h1 {
  font-size: 36px;
  font-family: cursive;
}

p {
  font-size: 24px;
  text-indent: 60px;
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

